<template>
    <div class="content newDetailsPage">
        <h2>{{articleInfo.title}}</h2>
        <div class="content_tap_box">
            <span class="content_tap_type">{{articleInfo.source}}</span>
            <span class="content_tap_time">{{articleInfo.time}}</span>
        </div>
        <div class="line"></div>
        <p class="article_content" v-html="articleInfo.content"></p>
    </div>
</template>

<script>
import {newsInfo} from "../../config/api";
import { dialogConfirm, dialogConfirmHasbtn } from "../../config/mUtils";
export default {
  data() {
    return {
      articleInfo:'',
    };
  },
  components: {},
  created() {
    if (this.$route.query.news_id != undefined) {
      this.getData();
    }
  },
  mounted() {},
  computed: {},
  methods: {
    getData() {
      console.log(1111);
      newsInfo({
          news_id:this.$route.query.news_id
      }).then(res => {
        console.log(res);
        this.articleInfo = res.data.payload;
        // this.list = res.data.payload.data;
        // this.total = res.data.payload.total;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
html {
  overflow-y: scroll;
}

:root {
  overflow-y: auto;
  overflow-x: hidden;
}

:root body {
  position: absolute;
}

body {
  width: 100vw;
  overflow: hidden;
}

.content {
  background-color: #ffffff;
  padding: 20px;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow: auto;
  h2 {
    font-size: 0.5333333333333333rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 0.48rem;
  }
  .content_tap_box {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    span {
      font-size: 0.32rem;
    }
    .content_tap_type {
      color: #1787fb;
      background-color: rgba(23, 135, 251, 0.1);
      padding: 0.05333333333333334rem 0.16rem;
      margin-left: 0.13333333333333333rem;
    }
    .content_tap_time {
      color: #a0a4aa;
      margin-left: 0.26666666666666666rem;
    }
  }
  .line {
    border-bottom: 1px solid #f1f1f1;
    margin: 0.3466666666666667rem 0 0.6666666666666666rem 0;
  }
  .article_content {
    font-size: 0.37333333333333335rem;
    color: #808080;
    font-family: "PingFangSC";
    letter-spacing: 0.02666666666666667rem;
    
  }
  
}
</style>



